export default {
  data() {
    return {
      dialog_visible: false
    };
  },

  components: {},
  props: {
    title: {
      type: String
    },
    titleColor: {
      type: String,
      default: '#000000'
    },
    logImage: {
      type: String
    },
    logName: {
      type: String
    },
    content: {
      type: String
    },
    contentColor: {
      type: String,
      default: '#888888'
    }
  },
  methods: {
    cancelCallback() {
      this.$emit('cancel');
    },
	onchangeVisible(status){
		console.log(status)
		if(status){
			this.dialog_visible = true;
		}else{
			this.dialog_visible = false;
		}
		console.log(this.dialog_visible)
	},
    onGotUserInfo(e) {
      this.$emit('confirm', e);
    }

  }
};
